import { Component, Event, EventEmitter, Prop, h } from '@stencil/core';
import { SWITCH_SIZES } from './exports';

@Component({
  tag: 'mdx-switch',
  styleUrl: 'switch.scss',
  shadow: true,
})
export class Switch {
  @Event()
  toggle: EventEmitter<{ selected: boolean }>;

  @Prop({ reflect: true })
  size: (typeof SWITCH_SIZES)[keyof typeof SWITCH_SIZES] = SWITCH_SIZES.MD;

  @Prop()
  disabled = false;

  @Prop({ reflect: true })
  selected = false;

  @Prop()
  // eslint-disable-next-line @stencil-community/reserved-member-names -- to enforece aria-label
  ariaLabel!: string;

  render() {
    return (
      <button
        class={`switch${this.selected ? ' switch--selected' : ''}`}
        onClick={() => {
          this.selected = !this.selected;
          this.toggle.emit({ selected: this.selected });
        }}
        aria-pressed={this.selected}
        disabled={this.disabled}
        aria-label={this.ariaLabel}
      >
        <span class="switch__nob" />
      </button>
    );
  }
}
